import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { map } from 'rxjs/internal/operators/map';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class WebInterfaceService {
  private baseUrl: string = environment.apiUrl;
  
  constructor(private http:HttpClient) { }

  getHomepageBanner():Observable<any>{
    return this.http.get(this.baseUrl+'banner/home-page-list').pipe(
      map(
        Response => {
          return Response;
        }
      )
    )
}
  getProductpageBanner():Observable<any>{
    return this.http.get(this.baseUrl+'banner/product-page-list').pipe(
      map(
        Response => {
          return Response;
        }
      )
    )
}

updateBanner(model):Observable<any>{
  return this.http.post(this.baseUrl+'banner/add-edit',model).pipe(
    map(
      Response => {
        return Response;
      }
    )
  )
}
  
}
